import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, AlertController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata'

@IonicPage()
@Component({
    selector: 'page-products',
    templateUrl: 'products.html',
})
export class ProductsPage {
  faqList: any = [];
  langs: any;
  anotherList: any = [];
  objectblock: any = { type:'products',  lang: '' };
  constructor( public navCtrl: NavController, private clientdata: ClientdataProvider, public globalVars: GlobalVarsProvider, public navParams: NavParams,
    public loadingController: LoadingController, public alertService: AlertServiceProvider, public alertCtrl: AlertController ) {
   
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
  
    this.submit();
  }

  ionViewDidLoad() {
    console.log( 'ionViewDidLoad ContactPage' );
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }

  goHome() {
    this.navCtrl.pop();
  }
  getItems( ev: any ) {
    this.list();
    let val = ev.target.value;
    if ( val && val.trim() != '' ) {
      this.faqList = this.faqList.filter(( utility ) => {
        return ( utility.question.toLowerCase().indexOf( val.toLowerCase() ) > -1 );
      } ) }
    else {
      this.list();
    }
  }

  list() { 
    this.faqList = this.anotherList;
  }

  submit() {
    this.alertService.showLoading( this.langs.submitting_txt );
      this.clientdata.faq( this.objectblock )
        .subscribe( data => {
          data = this.globalVars.testdec( data );
          console.log(data)
          if ( data.length != 0 ) {
            this.alertService.dismiss();
            if ( !data.error ) {
              this.anotherList = data.data;
              this.faqList = data.data;
            } else if ( data.error ) {
              this.alertService.errorPop( data.message );
              this.navCtrl.pop();
            } else {
              this.alertService.errorPop( this.langs.tech );
              this.navCtrl.pop();
            }
          } else {
            this.alertService.myLoadingDismiss();
            this.alertService.errorPop( this.langs.tech );
            this.navCtrl.pop();
          }

        }, err => {
          console.log( err );
          if ( err != 'Request timed out kindly try again later' ) {
            err = "Connection error";
          }
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
          this.navCtrl.pop();
        } );
    }

  }





